import { request } from 'utils';

const prefix = '/api/categories';

export const getEthnicity = () =>
  request({
    prefix,
    url: '/Ethnicity',
    method: 'GET',
    params: {},
    data: {},
  });
export const getEthnicityById = id =>
  request({
    prefix,
    url: `/Ethnicity/${  id}`,
    method: 'GET',
    params: {},
    data: {},
  });
export const createEthnicity = data =>
  request({
    prefix,
    url: '/Ethnicity',
    method: 'POST',
    params: {},
    data,
  });
export const updateEthnicity = (id, data) =>
  request({
    prefix,
    url: `/Ethnicity/${  id}`,
    method: 'PUT',
    params: {},
    data,
  });
export const deleteEthnicity = id =>
  request({
    prefix,
    url: `/Ethnicity/${  id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



